  position: relative;
  width: 100%;
  .el{
    display: inline-block;
    >div{
      margin: 0 10px;
    }
  }

  .dao{
    position: absolute;
    right: 10px;
    top: 20px;
    width: 85px;
    height: 30px;
    color: #fff;
    background: #5296D1;
    text-align: center;
    line-height: 30px;
    font-size: 14px;
    border-radius: 10px;
    span{
      display: inline-block;
      width: 15px;
      height: 15px;
      position: relative;
      top: 2px;
      background: url(../../assets/imgs/hole/tubiao.png);
      background-size: 100% 100%;
     }
  }
}

