<template>
  <div class="loop">
    <div class="top">
      <div class="el">
        <el-select v-model="value1" placeholder="事件分类" @change='change'>
            <el-option
              v-for="item in options1"
              :key="item.id"
              :label="item.name"
              :value="item.id">
            </el-option>
          </el-select>
          <el-select v-model="value2" placeholder="事件类型">
              <el-option
                v-for="item in options2"
                :key="item.id"
                :label="item.name"
                :value="item.id">
              </el-option>
            </el-select>
            <el-select v-model="value3" placeholder="威胁级别">
                <el-option
                  v-for="item in options3"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id">
                </el-option>
              </el-select>

          <el-date-picker
                v-model="time"
                type="daterange"
                :editable='false'
                start-placeholder="开始日期"
                end-placeholder="结束日期">
              </el-date-picker>
      </div>


      <div class="dao" @click='select'>
        <span></span>
        查询
      </div>


    </div>

    <div class="tiao">
      全部
    </div>
    <div class="table">

      <el-table
            :data="tableData.dataList"
            style="width: 100%">
            <el-table-column
                  type="index"
                  label="序号"
                  width="50">
                </el-table-column>
            <el-table-column
              prop="eventName"
              label="事件名称">
            </el-table-column>
            <el-table-column
              prop="attackType"
              label="事件分类">
            </el-table-column>
            <el-table-column
              prop="eventType"
              label="事件类型">
            </el-table-column>
            <el-table-column
              prop="sourceIp"
              label="攻击源">
            </el-table-column>
            <el-table-column
              prop="attackIp"
              label="攻击目的">
            </el-table-column>
            <el-table-column
              prop="eventLevel"
              label="威胁级别">
            </el-table-column>
            <el-table-column
              prop="idt"
              label="时间">
            </el-table-column>
          </el-table>
    </div>

    <div class="page-btn">
      <el-pagination
        background
        layout="prev, pager, next"
        :total='tableData.rowCount'
        :current-page='curPage'
        :page-size='pagesize'
        @current-change='changepage'>
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import {yjAttackGrid,dicMap} from '../../api/attack/loop.js'
  export default{
    mounted(){
      let _this = this;
      (function($){


        yjAttackGrid({
          rows: _this.pagesize
        }).then(res => {
          console.log('攻击管理',res);
          _this.tableData = res.result;
        })
        dicMap('AttackTypeLanYun').then(res => {//事件分类
          console.log('事件分类',res);
          _this.options1 = res;
        })
        dicMap('AttackEventLevelLanYun').then(res => {//威胁级别
          console.log('威胁级别',res);
          _this.options3 = res;
        })


      		    $.fn.placeholder = function(options){
      		        var opts = $.extend({}, $.fn.placeholder.defaults, options);
      		        var isIE = document.all ? true : false;
      		        return this.each(function(){
      		            var _this = this,
      		                placeholderValue =_this.getAttribute("placeholder"); //缓存默认的placeholder值
      		            if(isIE){
      		                _this.setAttribute("value",placeholderValue);
      		                _this.onfocus = function(){
      		                    $.trim(_this.value) == placeholderValue ? _this.value = "" : '';
      		                };
      		                _this.onblur = function(){
      		                    $.trim(_this.value) == "" ? _this.value = placeholderValue : '';
      		                };
      		            }
      		        });
      		    };
      		})(jQuery);
      		$("input").placeholder();
    },
    methods: {
      select(){
        console.log(this.time)
        yjAttackGrid({
          attackType: this.value1,
          eventType:this.value2,
          eventLevel:this.value3,
          eventTimeBegin: this.time.length?this.getTime(this.time[0])+" 00:00:00":"",
          eventTimeEnd: this.time.length?this.getTime(this.time[1])+" 00:00:00":"",
          page: this.curPage,
          rows: this.pagesize
        }).then(res => {
          console.log('攻击管理',res);
          this.tableData = res.result;
        })
      },
      changepage(ev){
        this.curPage = ev;
        yjAttackGrid({
          attackType: this.value1,
          eventType:this.value2,
          eventLevel:this.value3,
          eventTimeBegin: this.time.length?this.getTime(this.time[0])+" 00:00:00":"",
          eventTimeEnd: this.time.length?this.getTime(this.time[1])+" 00:00:00":"",
          page: this.curPage,
          rows: this.pagesize
        }).then(res => {
          console.log('攻击管理',res);
          this.tableData = res.result;
        })
      },
      getTime(date){
        if(!date) return;
        var YY = date.getFullYear() + '-';
        var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
        var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
        return YY + MM + DD;
      },
      change(a){
        let val = this.options1.filter((item,index) => {
          return item.id == a;
        })
        let obj = {
           "网络攻击":"AttackEventTypeNetLanYun",
           "恶意代码":"AttackEventTypeCodeLanYun",
           "威胁情报":"AttackEventTypeThreatenLanYun",
           "自定义":"AttackEventTypeCustomLanYun"
        }
        for(let key in obj){
          if(key == val[0].name){
            dicMap(obj[key]).then(res => {//事件类型
              console.log('事件类型',res);
              this.options2 = res;
            })
        }}
      }
    },
    data(){
      return{
        options1: [],
        options2: [],
        options3: [],
        startTime: '',
        endTime: '',
        value2: '',
        value3: '',
        value1: '',
        time: [],
        tableData: [],
        curPage: 1,//当前页数
        pagesize: 10,//每页显示条数
      }
    }
  }
</script>

<style lang='less' scoped>
  .right .top{
    position: relative;
    width: 100%;
    .el{
      display: inline-block;
      >div{
        margin: 0 10px;
      }
    }

    .dao{
      position: absolute;
      right: 10px;
      top: 20px;
      width: 85px;
      height: 30px;
      color: #fff;
      background: #5296D1;
      text-align: center;
      line-height: 30px;
      font-size: 14px;
      border-radius: 10px;
      span{
        display: inline-block;
        width: 15px;
        height: 15px;
        position: relative;
        top: 2px;
        background: url(../../assets/imgs/hole/tubiao.png);
        background-size: 100% 100%;
       }
    }
  }

</style>
<style lang='less'>
  .el-select,.el-input{
        width: 108px !important;
        height: 30px;
      }
      .el-date-editor{
        width: 265px !important;
      }
      .el-input__inner {
        height: 30px;
        background-color: #0B5CB9 !important;
        border: none;
        border-radius: 20px;
        font-size: 10px;
        color: #fff;
      }
      .el-input__icon{
        line-height: 30px;
      }
      .el-table th, .el-table tr{
        background: transparent;
      }
      .el-table, .el-table__expanded-cell{
        background: transparent;
      }
      .el-table td, .el-table th.is-leaf{
        border: none;
      }
      .el-table::before {
      	height: 0px;
      }
      .el-table tbody tr:hover>td {
          background-color:transparent!important
      }


      .el-table__body-wrapper .el-table__row{
        td:nth-child(1),td:nth-child(2),td:nth-child(3),td:nth-child(4){
          color: #8BAFBA;
        }

        td:nth-child(5){
          color: #E85259;
        }
        td:nth-child(6){
          color: #6794DD;
        }
        td:nth-child(7),td:nth-child(8),td:nth-child(9){
          color: #fff;
        }
        td:nth-child(10){
          color: #F4B890;
        }
      }



      .el-pagination.is-background .btn-next, .el-pagination.is-background .btn-prev, .el-pagination.is-background .el-pager li{
        background: transparent;
        border: 1px solid #fff;
        color: #93D7F5;
        font-weight: 400;
      }

      .el-pagination.is-background .el-pager li.active{
        border-color: transparent;
      }

  .page-btn{
            position: absolute;
            bottom: 50px;
            right: 30px;
          }
</style>
<style>
  .el-table .cell{
    text-align: center !important;
  }
    .el-range-editor .el-range-input{
      background: #0B5CB9;
    }
    .el-date-editor,.el-range-input{
      color: #fff !important;
    }
    .el-date-editor .el-range__icon{
      line-height: 25px;
    }
    .el-input__icon .el-range__close-icon{
      line-height: 25px;
    }
    .el-date-editor .el-range-separator{
      line-height: 25px;
      color: #fff;
    }
</style>
